export default {
	data() {
		return {
			InAudit: false,
			errorAudit:false,
			Audit:false,
			isCheck: "0",
			formList: [{
				label: '姓名',
				val: '',
				name: 'real_name',
				type: 'text',
				length: 15
			}, {
				label: '手机号',
				val: '',
				name: 'phone',
				type: 'number',
				length: 11
			}, {
				label: '所在地区',
				val: '',
				name: 'address',
				type: 'text',
				length: 20
			}, {
				label: '身份证号',
				val: '',
				name: 'id_number',

				type: 'idCard',




				length: 20
			}, {
				label: '寺庙名称',
				val: '',
				name: 'name',
				type: 'text',
				length: 20
			}],
			isRealAuth: false,
			positive:'',
			reverseImg:'',
			yingye:'',
			id_images:''
		}
	},
	methods: {
		resetReq(){
			this.errorAudit=false;
		},
		checkBox: function(e) {
			if (Number(this.isCheck)) {
				this.isCheck = "0"
			} else {
				this.isCheck = "1"
			}
		},
		navTo(url) {
			this.$commonJs.navTo(url)
		},
		joinNow() {
			let Rules = [{
					name: 'real_name',
					type: 'required',
					errmsg: '请输入真实姓名'
				},
				{
					name: 'phone',
					type: 'required',
					errmsg: '请输入您的手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				},
				{
					name: 'address',
					type: 'required',
					errmsg: '请输入选择您所在地区'
				},
				{
					name: 'name',
					type: 'required',
					errmsg: '请输入寺庙名称'
				}					
			];
			var formData = {};
			this.formList.forEach((item) => {
				formData[item.name] = item.val
			})
			let valLoginRes = this.$validate.validate(formData, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			if (!this.isRealAuth) {
				this.$commonJs.uniMsg("请完成实名认证！")
				return false
			}
			formData.id_images=this.id_images;
			this.$apiJs.setTempleJoin(formData).then(res => {
				if (res){
					// this.$commonJs.uniMsg('已提交，等待审核', 'success');
					this.InAudit=true;
					// this.$commonJs.navBack();
				}
			})
		},
		getState() {
			// 状态值: -1=审核不通过,0=待审核
			this.$apiJs.getTempleJoinStatus().then(res => {
				if (res.status==0) {
					// this.$commonJs.uniMsg('已提交，等待审核', 'success');
					this.InAudit=true;
				}else if (res.status==-1){
					this.errorAudit=true;
				}
			})
		}
	},
	onLoad(option) {
		if(option.status==-1){
			this.errorAudit=true;
		}else{
			this.getState();
		}
	}
}
